const maybeNoun =
  '(rose|robin|dawn|ray|holly|bill|joy|viola|penny|sky|violet|daisy|melody|kelvin|hope|mercedes|olive|jewel|faith|van|charity|miles|lily|summer|dolly|rod|dick|cliff|lane|reed|kitty|art|jean|trinity)'
const maybeVerb = '(pat|wade|ollie|will|rob|buck|bob|mark|jack)'
const maybeAdj = '(misty|rusty|dusty|rich|randy)'
const maybeDate = '(april|june|may|jan|august|eve)'
const maybePlace = '(paris|alexandria|houston|kobe|salvador|sydney)'

const fixPerson = function(doc) {
  // clues from honorifics
  let hon = doc.if('#Honorific')
  if (hon.found === true) {
    //mr Putin
    doc.match('(mr|mrs|ms|dr) (@titleCase|#Possessive)+').tagUnsafe('#Person', 'mr-putin')
    //mr X
    hon.match('#Honorific #Acronym').tagUnsafe('Person', 'Honorific-TitleCase')
    //remove single 'mr'
    hon.match('^#Honorific$').unTag('Person', 'single-honorific')
    //first general..
    hon.match('[(1st|2nd|first|second)] #Honorific').tagUnsafe('Honorific', 'ordinal-honorific')
  }

  //methods requiring a titlecase
  let title = doc.if('@titleCase')
  if (title.found === true) {
    title.match('#Acronym @titleCase').tagSafe('#Person', 'acronym-titlecase')
    //ludwig van beethovan
    title.match('@titleCase (van|al|bin) @titleCase').tagSafe('Person', 'titlecase-van-titlecase')
    //jose de Sucre
    title.match('@titleCase (de|du) la? @titleCase').tagSafe('Person', 'titlecase-van-titlecase')
    //Foo U Ford
    title
      .match('[#ProperNoun] #Person')
      .notIf('@hasComma')
      .tagSafe('Person', 'proper-person')

    //pope francis
    title
      .match('(lady|queen|sister) @titleCase')
      .ifNo('#Date')
      .ifNo('#Honorific')
      .tagUnsafe('#FemaleName', 'lady-titlecase')
    title
      .match('(king|pope|father) @titleCase')
      .ifNo('#Date')
      .tagUnsafe('#MaleName', 'poe')

    // jean Foobar
    title.match(maybeNoun + ' #Acronym? @titleCase').tagSafe('Person', 'ray-smith')
    // rob Foobar
    title.match(maybeVerb + ' #Acronym? @titleCase').tagUnsafe('Person', 'rob-smith')
    // rusty Foobar
    title.match(maybeAdj + ' #Acronym? @titleCase').tagUnsafe('Person', 'rusty-smith')
    // june Foobar
    title.match(maybeDate + ' #Acronym? (@titleCase && !#Month)').tagUnsafe('Person', 'june-smith')
  }

  let person = doc.if('#Person')
  if (person.found === true) {
    //Frank jr
    person.match('#Person (jr|sr|md)').tagUnsafe('Person', 'person-honorific')
    //peter II
    person.match('#Person #Person the? #RomanNumeral').tagUnsafe('Person', 'roman-numeral')
    //'Professor Fink', 'General McCarthy'
    person.match('#Honorific #Person').tagUnsafe('Person', 'Honorific-Person')
    // 'john E rockefeller'
    person.match('#FirstName [/^[^aiurck]$/]').tagUnsafe(['Acronym', 'Person'], 'john-e')
    //Doctor john smith jr
    person.match('#Honorific #Person').tagUnsafe('Person', 'honorific-person')
    //general pearson
    person
      .match('[(private|general|major|corporal|lord|lady|secretary|premier)] #Honorific? #Person')
      .tagUnsafe('Honorific', 'ambg-honorifics')
    //Morgan Shlkjsfne
    title
      .match('#Person @titleCase')
      .match('@titleCase #Noun')
      .tagSafe('Person', 'person-titlecase')
    //a bunch of ambiguous first names

    //Nouns: 'viola' or 'sky'
    let ambigNoun = person.if(maybeNoun)
    if (ambigNoun.found === true) {
      // ambigNoun.match('(#Determiner|#Adverb|#Pronoun|#Possessive) [' + maybeNoun + ']').tagUnsafe('Noun', 'the-ray')
      ambigNoun.match(maybeNoun + ' #Person').tagSafe('Person', 'ray-smith')
    }

    //Verbs: 'pat' or 'wade'
    let ambigVerb = person.if(maybeVerb)
    if (ambigVerb === true) {
      ambigVerb.match('(#Modal|#Adverb) [' + maybeVerb + ']').tagUnsafe('Verb', 'would-mark')
      ambigVerb.match(maybeVerb + ' #Person').tagUnsafe('Person', 'rob-smith')
    }

    //Adjectives: 'rusty' or 'rich'
    let ambigAdj = person.if(maybeAdj)
    if (ambigAdj.found === true) {
      ambigAdj.match('#Adverb [' + maybeAdj + ']').tagUnsafe('Adjective', 'really-rich')
      ambigAdj.match(maybeAdj + ' #Person').tagUnsafe('Person', 'randy-smith')
    }

    //Dates: 'june' or 'may'
    let ambigDate = person.if(maybeDate)
    if (ambigDate.found === true) {
      ambigDate.match(maybeDate + ' #ProperNoun').tagUnsafe(['FirstName', 'Person'], 'june-smith')
      ambigDate.match('(in|during|on|by|before|#Date) [' + maybeDate + ']').tagUnsafe('Date', 'in-june')
      ambigDate.match(maybeDate + ' (#Date|#Value)').tagUnsafe('Date', 'june-5th')
    }

    //Places: paris or syndey
    let ambigPlace = person.if(maybePlace)
    if (ambigPlace.found === true) {
      ambigPlace.match('(in|near|at|from|to|#Place) [' + maybePlace + ']').tagSafe('Place', 'in-paris')
      ambigPlace.match('[' + maybePlace + '] #Place').tagSafe('Place', 'paris-france')
      // ambigPlace.match('[' + maybePlace + '] #Person').tagSafe('Person', 'paris-hilton')
    }

    //this one is tricky
    let al = person.if('al')
    if (al.found === true) {
      al.match('al (#Person|@titleCase)').tagSafe('#Person', 'al-borlen')
      al.match('@titleCase al @titleCase').tagSafe('#Person', 'arabic-al-arabic')
    }

    let firstName = person.if('#FirstName')
    if (firstName.found === true) {
      //ferdinand de almar
      firstName.match('#FirstName de #Noun').tagUnsafe('#Person', 'firstname-de-noun')
      //Osama bin Laden
      firstName.match('#FirstName (bin|al) #Noun').tagUnsafe('#Person', 'firstname-al-noun')
      //John L. Foo
      firstName.match('#FirstName #Acronym @titleCase').tagUnsafe('Person', 'firstname-acronym-titlecase')
      //Andrew Lloyd Webber
      firstName.match('#FirstName #FirstName @titleCase').tagUnsafe('Person', 'firstname-firstname-titlecase')
      //Mr Foo
      firstName.match('#Honorific #FirstName? @titleCase').tagUnsafe('Person', 'Honorific-TitleCase')
      //peter the great
      firstName.match('#FirstName the #Adjective').tagUnsafe('Person', 'determiner5')

      //very common-but-ambiguous lastnames
      firstName
        .match('#FirstName (green|white|brown|hall|young|king|hill|cook|gray|price)')
        .tagUnsafe('#Person', 'firstname-maybe')

      //John Foo
      firstName
        .match('#FirstName @titleCase @titleCase?')
        .match('#Noun+')
        .tagUnsafe('Person', 'firstname-titlecase')
      //Joe K. Sombrero
      firstName
        .match('#FirstName #Acronym #Noun')
        .ifNo('#Date')
        .tagUnsafe('#Person', 'n-acro-noun')
        .lastTerm()
        .tagUnsafe('#LastName', 'n-acro-noun')

      // Dwayne 'the rock' Johnson
      firstName
        .match('#FirstName [#Determiner #Noun] #LastName')
        .tagUnsafe('#NickName', 'first-noun-last')
        .tagUnsafe('#Person', 'first-noun-last')

      //john bodego's
      firstName
        .match('#FirstName (#Singular|#Possessive)')
        .ifNo('(#Date|#Pronoun|#NickName)')
        .tagUnsafe('#Person', 'first-possessive')
        .lastTerm()
        .tagUnsafe('#LastName', 'first-possessive')

      // Firstname x (dangerous)
      let tmp = firstName
        .match('#FirstName (#Noun|@titleCase)')
        .ifNo('^#Possessive')
        .ifNo('#ClauseEnd .')
        .ifNo('#Pronoun')
      tmp.lastTerm().tagUnsafe('#LastName', 'firstname-noun')
    }

    let lastName = person.if('#LastName')
    if (lastName.found === true) {
      //is foo Smith
      lastName.match('#Copula [(#Noun|#PresentTense)] #LastName').tagUnsafe('#FirstName', 'copula-noun-lastname')
      // x Lastname
      lastName
        .match('[#Noun] #LastName')
        .canBe('#FirstName')
        .tagUnsafe('#FirstName', 'noun-lastname')
      //ambiguous-but-common firstnames
      lastName
        .match(
          '[(will|may|april|june|said|rob|wade|ray|rusty|drew|miles|jack|chuck|randy|jan|pat|cliff|bill)] #LastName'
        )
        .tagUnsafe('#FirstName', 'maybe-lastname')
      //Jani K. Smith
      lastName
        .match('(@titleCase|#Singular) #Acronym? #LastName')
        .ifNo('#Date')
        .tagUnsafe('#Person', 'title-acro-noun')
        .lastTerm()
        .tagUnsafe('#LastName', 'title-acro-noun')
    }
  }

  return doc
}
module.exports = fixPerson
